.nav-footer {
  @media (max-width: $medium-width) {
    width: 60%;
    margin-right: 34px;
  }

  @media (max-width: $small-width) {
    width: 100%;
    margin-bottom: 25px;
  }
}

.nav-footer ul {
  @include reset-list;
  display: grid;
  grid-template-columns: auto 35.7%;
  grid-template-rows: repeat(4, auto);
  grid-auto-flow: column;
  gap: 19px 17.6%;

  @media (max-width: 1340px) {
    gap: 30px 5%;
  }

  @media (max-width: $medium-width) {
    gap: 18px 5%;
  }

  @media (max-width: $small-width) {
    @include display-flex-wrap;
    width: 100%;
    min-height: min-content;
  }
}

.nav-footer li {
  width: 100%;

  @media (max-width: $small-width) {
    &:nth-child(4n+4) {
      margin-bottom: 20px;
    }
  }
}

.nav-footer a {
  vertical-align: bottom;
  line-height: 21px;
  color: inherit;
  text-decoration: none;
  text-transform: capitalize;

  &:hover,
  &:focus {
    box-shadow: 0 0 5px $basic-white;
  }

  &:active {
    outline: 1px solid $basic-white;
  }
}
